/**
 * 
 */
export class Cubic {
    px3: number
    px2: number
    px1: number
    py3: number
    py2: number
    py1: number
    epsilon: number
    constructor (a: number, b: number, c: number, d: number) {
      this.px3 = 3 * a
      this.px2 = 3 * (c - a) - this.px3
      this.px1 = 1 - this.px3 - this.px2
      this.py3 = 3 * b
      this.py2 = 3 * (d - b) - this.py3
      this.py1 = 1 - this.py3 - this.py2
      this.epsilon = 1e-7      // 
    }
  
    getX(t: number) {
      return ((this.px1 * t + this.px2) * t + this.px3) * t
    }
  
    getY(t: number) {
      return ((this.py1 * t + this.py2) * t + this.py3) * t    
    }
  
    solve(x: number) {
      if (x === 0 || x === 1) {             //  0  1  t 
        return this.getY(x)
      } 
      let t = x
      for (let i = 0; i < 8; i++) {         //  8 
        let g = this.getX(t) - x
        if (Math.abs(g) < this.epsilon) {   // 
          return this.getY(t)
        }
        let d = (3 * this.px1 * t + 2 * this.px2) * t + this.px3   //  x 
        if (Math.abs(d) < 1e-6) {           // 
          break
        }
        t = t - g / d
      }
      return this.getY(t)                   //  t  y
    }
  }
  